import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)


// /
export default new Vuex.Store({
	// statedata this.$store.state.
	state:{
		count:1,
	},
	cinxy:{
		cin:1,
	},
	
	
	// mutations this.$store.commit('',)
	mutations:{
		add(state,n){
			state.count+=n;
		},
		sub(state,n){
			state.count-=n;
		},
		ciny(cin,n){
			cinxy.cin+=n;
		},
		cinx(cin,n){
			cinxy.cin-=n;
		}
		
		
	}
})


